.grey-bg {
  background-color: $grey;
}
.filter-container {
  display: none;
  padding: 50px 0px;
  width: 97%;
  margin-left: auto;
  margin-right: auto;
  @media only screen and (min-width: 768px) {
    /*padding: 25px;*/
    width: 60%;
    display: flex;
    /*justify-content: space-between;*/
    background-color: $grey;

    .filter {
      display: flex;
      /*justify-content: space-between;*/
      border-right: 1px solid $dark-grey;
      margin-right: 10px;
      padding-right: 40px;
    }
    .filter > h2 {
      font-size: 30px;
      margin: 7px 10px 0 0;
    }
    .filter > .filter-buttons {
      text-align: left;
      margin-top: 5px;
    }
    .refine-button {
      padding: 10px;
      float: left;
    }
  }
}



