.school-heading-container {
  /* background-color: $grey; */
  background-color: blue;
  width: 100%;
}
.school-heading {
  /*display: flex;
  justify-content: space-between;
  align-items: center; */
  padding: 50px 0px;
  margin-left: auto;
  margin-right: auto;
  margin-top: -100px;
  padding-top: 100px;
  width: 100%;
  box-sizing: border-box;
  @media only screen and (min-width: 768px) {
    width: 60%;
  }

  .school-info {
    display: flex;
    align-items: center;
  }

  .school-info h1 {
    text-align: center !important;
  }
  .school-info > img {
    margin-right: 20px;
    max-width: 150px;
    height: auto;
    background-color: $grey;
    /*background: none;*/
  }
  .school-name {
    font-family: 'Montserrat Ultra Light';
  }
  .school-link {
    margin: 10px 0;
    text-align: center;
  }
  .school-link > a {
    /*display: flex;*/
    align-items: center;
  }
  .school-link img {
    margin: 0px;
  }
  .school-links {
    text-align: center;
  }
  .video-link {
    margin: 15px 30px;
   
    font-family: 'Bebas Neue';
    color: $white;
  }
}
.school-courses-link {
  padding: 40px 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color:$grey;
}

.school-courses-link > p{
  color: $white;
  font-family: 'Gotham Bold';
}

// student work section
.student-work{
  height: 50vh;
  background-color: red;
}

.student-work > p{
  margin: 0;
}

.school-overview-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;

  h1{
    margin: 0;
  }
  .school-logo{
    width: 20%;
  }
  .school-overview{
    width: 30%;
    padding: 5px;
  }
  .school-fees{
    width: 30%;
    padding: 5px;
  }
  .school-links{
    width: 20%;
  }
  .details-box{
    background-color: white;
    width: 100%;
    position: relative;
    top: -170px;
    padding-top: 40px;
    height: 400px;
  }
}

// study areas
.study-areas{
  width: 100%;
  height: 50vh;
  background-color: white;
}

//other section
.other-section{
  display: flex;
  justify-content: space-between;

  .locations{
    width: 33.33%;
    background-color: blue;
  }
  .facilities{
    width: 33.33%;
    background-color: red;
  }
  .alumni{
    width: 33.33%;
    background-color: blue;
  }

  .other-section-box{
    width: 100%;
    height: 60vh;
    display: flex;
    align-items: center;

    h2{
      width: 100%;
      text-align: center;
    }
  }

}
// School Details Page
.school-details-links {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .video-link {
    margin-right: 40px;
    text-align: center;
    text-transform: uppercase;
  }
  .video-link img {
    margin: 0px 15px;
  }
  .video-link a {
    text-decoration: none;
  }
  .video-link span {
    font-size: 11px;
    color: $black;
  }
  .other-links > a {
    display: block;
    margin: 10px 0px;
    width: 70px;
    text-align: center;
  }
}
.school-details {
  display: flex;
  justify-content: space-around;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  padding: 50px 0px;
  background-color: $grey;

  h2 {
    font-size: 30px;
    margin: 0px;
  }
  h3 {
    margin-bottom: 0px;
    font-size: 18px;
  }
  h4 {
    margin-bottom: 5px;
    margin-top: 10px;
    font-size: 13px;
  }
  p {
    margin-top: 0px;
    font-size: 15px;
    color: $white;
  }
  li, p {
    font-family: 'Montserrat Ultra Light';
    color: $white;
  }
  .overview {
    width: 60%;
    padding-right: 15px;
  }
  .overview > a {
    display: block;
    width: 30%;
    margin: 10px 0px;
    text-align: center;
  }
  .fees-scholarships, .study-areas {
    border-left: 1px solid $dark-grey;
    padding-left: 15px;
    padding-right: 15px;

    ul {
      list-style: none;
      margin: 0px;
      padding: 0px;
    }
    ul > li {
      padding-top: 5px;
    }
  }
  .fees-scholarships {
    font-size: 10px;
    width: 35%;

    .campuses, .delivery {
      width: 45%;
      display: inline-block;
    }
    .campuses {
      margin-right: 5%;
    }
  }
  .study-areas {
    width: 15%;
  }
  .study-areas > ul > li {
    padding-top: 10px;
    font-size: 11px;
  }
}
.school-locations {
  text-align: center;
  background-color: $grey;
  padding: 25px;
  p {
    width: 40%;
    margin: 0px auto;
    margin-bottom: 25px;
    font-family: 'Montserrat Ultra Light';
    color: $white;
  }
}
.school-facilities {
  padding: 25px;
  background-color: #3B3D3F;
  text-align: center;
  p {
    width: 40%;
    margin: 10px auto;
    margin-bottom: 50px;
    font-family: 'Montserrat Ultra Light';
    color: $white;
  }
}
.school-alumni {
  padding: 25px;
  text-align: center;
  background-color: $grey;
}
.school-details-subheading {
  font-size: 45px;
  margin-bottom: 0px;
}
.final-links {
  padding: 25px;
  background-color: $pink;
  text-align: center;
}
.final-links > .btn{
  margin: 0px 10px;
  border: 1px solid $black;
}
.school-footer {
  display: flex;
  .school-footer-section {
    flex-grow: 1;
    p {
      font-family: 'Gotham Bold';
    }
  }
  .school-footer-section:first-child {
    border-right: 1px solid #E4E9EC;
  }
}



